import React from 'react'
import { Link, useLocation, useHistory } from 'react-router-dom'

const Navbar = () => {
    let history = useHistory();
    const handleLogOut = () => {
        localStorage.removeItem('authToken');
        history.push('/');
    }
    let location = useLocation();

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <Link className="navbar-brand" to="/home">iNotebook</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={`nav-link ${location.pathname === "/home" ? "active" : ""}`} aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${location.pathname === "/about" ? "active" : ""}`} to="/about">About</Link>
                        </li>
                    </ul>
                    {!localStorage.getItem('authToken') ?
                        <form className="d-flex">
                            <Link to="/login" className="btn btn-primary mx-1" role="button">Log In</Link>
                            <Link to="/signup" className="btn btn-primary mx-1" role="button">Sign Up</Link>
                        </form> : <div className="d-flex">
                            <button onClick={handleLogOut} className="btn btn-primary" >Log Out</button>
                        </div>
                    }
                </div>
            </div>
        </nav >
    )
}

export default Navbar;